import React, { useState, useEffect } from 'react';
import { Link } from 'react-router-dom';
import axios from 'axios';

const Task = props => (
  <tr>
    <td className={props.task.completed ? 'completed' : ''}>{props.task.title}</td>
    <td className={props.task.completed ? 'completed' : ''}>{props.task.description}</td>
    <td className={props.task.completed ? 'completed' : ''}>{props.task.priority}</td>
    <td className={props.task.completed ? 'completed' : ''}>{props.task.dueDate.substring(0,10)}</td>
    <td>
      <Link to={"/edit/"+props.task._id}>edit</Link> | <a href="#" onClick={() => { props.deleteTask(props.task._id) }}>delete</a>
    </td>
  </tr>
)

const TasksList = () => {
  const [tasks, setTasks] = useState([]);

  useEffect(() => {
    axios.get('http://localhost:5000/tasks/')
      .then(response => {
        setTasks(response.data);
      })
      .catch((error) => {
        console.log(error);
      })
  }, []);

  const deleteTask = (id) => {
    axios.delete('http://localhost:5000/tasks/'+id)
      .then(res => console.log(res.data));
    setTasks(tasks.filter(el => el._id !== id));
  }

  const taskList = () => {
    return tasks.map(currenttask => {
      return <Task task={currenttask} deleteTask={deleteTask} key={currenttask._id}/>;
    })
  }

  return (
    <div>
      <h3>Logged Tasks</h3>
      <table className="table">
        <thead className="thead-light">
          <tr>
            <th>Title</th>
            <th>Description</th>
            <th>Priority</th>
            <th>Due Date</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          { taskList() }
        </tbody>
      </table>
    </div>
  );
};

export default TasksList;